.sidebar {
    display: none;
}

.sidebar.open {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999;
    display: none;
    width: 317px;
    background-color: #fafafa;
    display: flex;
    justify-content: left;
    align-items: center;
}

.background {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    display: none;
    background-color: #a2a09f85;
    opacity: 1;
    display: flex;
    justify-content: left;
    align-items: center;
}

.nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 65px;
    left: 70px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 22px;
}

.nav a {

    color: #000;
    text-decoration: none;
    line-height: 46px;
}

.nav a:hover {
    color: #58acff;
}

.nav a:active {
    color: #3d6b99;
}

.nav a.active {
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: #0059b2;
}

.close {
    width: 24px;
    height: 24px;
    border: none;
    background-color: #fafafa;
    position: fixed;
    left: 280px;
    top: 15px;
}

.close img {}