.greetingForm {
  font-family: Montserrat;
  font-size: 14px;
}

.inputAndButtonContainer {
  display: flex;
  flex-direction: row;
  margin: 10px;
}

.error {
  color: red;

}

.input {
  border: 1px solid rgb(53, 51, 51);
  width: 375px;
  height: 35px;
  border-radius: 3px;
  margin-right: 10px;

}

.input:focus {}

.errorInput {
  border: 1px solid red;
  width: 375px;
  height: 35px;
  border-radius: 3px;
  margin-right: 10px;
}

.button {
  width: 80px;
  height: 35px;
  padding: 5px 24px 5px 24px;
  gap: 10px;
  border-radius: 3px;
  opacity: 0.5px;
  background-color: #0066CC;
  color: #FFFFFF;
}

.button:focus {
  background-color: #00cc25;
}

.button:active {
  
}

.button:disabled {
  background-color: #8ab9e7;
}

.text {
  font-weight: 400;
  line-height: 17px;
  text-align: left;
  color: #00000080;
}

.greeting {
  font-weight: 500;
  line-height: 17.07px;
  text-align: left;
  color: #000000;
}