.header {
    width: 100%;
    height: 60px;
    box-shadow: 0 0 2px 0 rgba(29, 33, 38, 0.1), 0 0 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    position: relative;
}

.header h1 {
    margin-right: 70px;
    margin-top: 20px;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 24px;
    color: #000;
}