.radio {
    appearance: none;
    width: 16px;
    height: 16px;
    border: 2px solid black;
    border-radius: 50%;
    margin-right: 10px;
    outline: none;
    cursor: pointer;
    position: relative;
}


.radio::before {}


.radio:checked::before {
    content: '';
    display: block;
    width: 9px;
    height: 9px;
    background-color: black;
    /* Цвет после нажатия */
    border-radius: 50%;
    position: absolute;
    top: 2px;
    left: 2px;
}

.label {
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
    font-size: 16px;
    color: #000;
}

.options {
    display: flex;
    flex-direction: column;
}